import React from 'react'

export const LoginPage = ({ history }) => {

    const handleLogin = () => {
        history.push('/');
    }

    return (
        <div className='container mt-5'>
            <h3>Inicio de sesión</h3>
            <hr />

            <button 
                className='btn btn-outline-info'
                onClick={ handleLogin }
            >
                Login
            </button>
        </div>
    )
}
